body {
  max-width: 1400px;
  background: linear-gradient(90deg, #05421c, #000);
  margin: auto;
  display: block;
}

.cat-info {
  flex-direction: row;
  gap: 30px;
  margin-top: 30px;
  display: flex;
}

.content {
  width: 400px;
}

.image {
  border: 3px solid #000;
  box-shadow: 5px 0 10px rgba(248, 248, 248, .3);
}

.text {
  width: 400px;
  color: #000;
  color: #fff;
  background: linear-gradient(90deg, #05421c, #000);
  border: 2px solid #000;
  padding: 30px;
  transition: all .3s;
  box-shadow: inset 5px 5px 10px rgba(248, 248, 248, .3);
}

.loader {
  width: 48px;
  height: 48px;
  box-sizing: border-box;
  border: 3px solid #fff;
  border-bottom-color: rgba(0, 0, 0, 0);
  border-radius: 50%;
  animation: rotation 1s linear infinite;
  display: inline-block;
  position: relative;
}

.loader:after {
  content: "";
  box-sizing: border-box;
  border: 10px solid rgba(0, 0, 0, 0);
  border-right-color: #fff;
  position: absolute;
  top: 31px;
  left: 20px;
  transform: rotate(-40deg);
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

